@import "../../assets/style/variables.scss";

.services_item_content {
  position: relative;
  max-width: 1000px;
  width: 100%;

}

.services_item {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 10px;
  max-width: 1000px;



  p {
    color: black;
    font-weight: 700;
    font-size: calc($fontSize16 + 10 * (100vw / 1920));
    cursor: pointer;
    text-align: center;
  }

  span {
    transform: rotate(0deg);
    transition: .3s;
    font-size: $fontSize26;
    color: $white;

    &.active {
      transform: rotate(180deg);

    }

  }

  @media screen and (max-width: $md2 + px) {
    flex-direction: column;

  }
}
.spoiler_block_img{
  display: flex;
  align-items: center;
  justify-content: center;
  img{
    width: 70%;
    height: 70%;
  }

}

.services_item_content:nth-child(2) {
  .services_item {
    @media screen and (max-width: $md2 + px) {
      flex-direction: column-reverse;


    }
  }
}


.services_item_content:nth-child(4) {
  .services_item {
    @media screen and (max-width: $md2 + px) {
      flex-direction: column-reverse;

    }
  }
}

.services_item_content:nth-child(6) {
  .services_item {
    @media screen and (max-width: $md2 + px) {
      flex-direction: column-reverse;

    }
  }
}

.services_item_text {
  padding: 20px;
  background-color: $mainColor;
  border-radius: 10px;
  width: 70%;

  p {
    color: $white;

  }
}

